	module.exports = {"data":{"wordpressPost":{"title":"Volcanoes, drug launches and type 2 diabetes: Most read articles in March","content":"<p>&nbsp;</p>\n<p>The most-read article in March was Christine Clar and colleagues&#8217; systematic review of SGLT2 receptor inhibitors in type 2 diabetes. Doyle <em>et al</em>&#8216;s  review on the links between patient experience and clinical safety &#8211; originally published in January &#8211; remains popular,  and Katzmarzyk <em>et al&#8217;s</em> article discusses sedentary behaviour and life expectancy in the USA also proves popular as the third most-read article this month.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Clar <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/5/e001007.full\">Systematic review of SGLT2 receptor inhibitors in dual or triple therapy in type 2 diabetes</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Doyle <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/1/e001570.full\">A systematic review of evidence on the links between patient experience and clinical safety and effectiveness</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Katzmarzyk <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/4/e000828.full\">Sedentary behaviour and life expectancy in the USA: a cause-deleted life table analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Gross <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e001844.full\">A novel model-based meta-analysis to indirectly estimate the comparative efficacy of two medications: an example using DPP-4 inhibitors, sitagliptin and linagliptin, in treatment of type 2 diabetes mellitus</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Langton <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e001541.full\">The clinical implications of elevated blood metal ion concentrations in asymptomatic patients with MoM hip resurfacings: a cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Ward <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/2/e002088.full\">Decline in new drug launches: myth or reality? Retrospective observational study using 30 years of data from the UK</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Ford <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/3/e002269.full\">Current treatments in diabetic macular oedema: systematic review and meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Carlsen <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/6/e001851.full\">Health effects following the Eyjafjallajökull volcanic eruption: a cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Herman <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/5/e001046.full\">Are complementary therapies and integrative care cost-effective? A systematic review of economic evaluations</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Smith <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/3/2/e002226.full\">An evaluation of the performance of the NHS Health Check programme in identifying people at high risk of developing type 2 diabetes</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"d1e9b5e0-bb8a-5e73-a89a-ba73aae92e0d"}}

